import React, { useEffect, useState } from "react";

// components
import FileExplorer from "./test2";
import FileController from "./test3";

// css
import { Card, Col, Container, Form, Row } from "react-bootstrap";


function AnalysisPage(){

    const [userFiles, setUserFiles] = useState({});
    const [selectedFolder, setSelectedFolder] = useState({});
    const [isLoading, setIsLoading] = useState(false);

    // showDirectoryPicker    
    const [notExistFunc, setNotExistFunc] = useState(false);
    const [notExistFuncFiles,setExistFuncFiles] = useState([]);

    //   handleDirectoryEntry  & initItems(,  )  
    const handleFileInput = async () => {

        try{
            setIsLoading(true);
            if (typeof window.showDirectoryPicker !== "function"){
                setNotExistFunc(true);
                return;
            } 
            const dirHandle = await window.showDirectoryPicker();
            let initItems = {isFolder:true, name:dirHandle.name, path:`~/${dirHandle.name}`, folderEntry:dirHandle, items:[]}
            await handleDirectoryEntry({ folderEntry:initItems.folderEntry, items:initItems.items, prevPath:initItems.path });
            setUserFiles(initItems);
            setSelectedFolder({});
        }catch(err){
            console.log(err);
        }finally{
            setIsLoading(false);
        }
    };


    //     initItems     
    async function handleDirectoryEntry({ folderEntry, items, prevPath }) {
        try{
            let curIdx = 0;
            for await (const entry of folderEntry.values()) {
                if (entry.kind === "file"){
                    const file = await entry.getFile();
                    items.push({isFolder:false, name:file.name, file, path: `${prevPath}/${file.name}` });
                    curIdx++;
                }
                if (entry.kind === "directory") {
                    items.push({isFolder:true, name:entry.name, folderEntry:entry, items:[], path: `${prevPath}/${entry.name}`})
                    await handleDirectoryEntry({ folderEntry:items[curIdx].folderEntry, items:items[curIdx].items, prevPath:items[curIdx].path });
                    curIdx++;
                }
            }
        }catch(err){
            console.log(err);
            setIsLoading(false);
        }
    }

    //       
    const handleUploadImageFiles = async () => {

        // showDirectoryPicker    
        if(notExistFunc){
            if(notExistFuncFiles.length){
                // let analysisData = new FormData();

                // for (let i = 0; i < notExistFuncFiles.length; i++) { 
                //     analysisData.append("files", notExistFuncFiles[i]);
                // }

                // analysis(analysisData).then((res) => {
                //     console.log(res);
                // }).catch((err) => {
                //     console.error(err);
                // })
                console.log('notExistFuncFiles',notExistFuncFiles)
            }else{
                alert('    .');
            }

            return;
        }

        // showDirectoryPicker   
        if(Object.keys(selectedFolder).length>0){
            const selectedFiles = [];

            selectedFolder.items.forEach(e=>{if(!e.isFolder) selectedFiles.push(e.file)})

            if (selectedFiles.length > 0) {
                console.log('selectedFiles', selectedFiles);

                // let analysisData = new FormData();

                // for (let i = 0; i < selectedFiles.length; i++) { 
                //     analysisData.append("files", selectedFiles[i]);
                // }

                // analysis(analysisData).then((res) => {
                //     console.log(res);
                // }).catch((err) => {
                //     console.error(err);
                // })
                
            }else{
                alert('    .')
            }
        }else{
            alert('    .')
        }
    }

    const handleSelectFolder = (explorer) => {
        setSelectedFolder(explorer);
        console.log('explorer',explorer);
    }

    useEffect(()=>{
        if(typeof window.showDirectoryPicker !== "function"){
            setNotExistFunc(true);
        }
    },[])

    return (
        <Container className="mt-5 mb-5">
            <Row>
                <Col className="col-6">
                    {!notExistFunc ? <FileExplorer
                        userFiles={userFiles}
                        handleSelectFolder={handleSelectFolder}
                        selectedFolder={selectedFolder}
                    /> :
                    <Card className="shadow CustomCard">
                        <Card.Body className="AnalysisInfo">
                        <Form.Control type="file" webkitdirectory="true" onChange={(e)=>setExistFuncFiles(e.target.files)} />
                        </Card.Body>
                    </Card>
                    }
                </Col>
                <Col className="col-6">
                    <FileController
                        userFiles={userFiles}
                        selectedFolder={selectedFolder}
                        handleFileInput={handleFileInput}
                        handleUploadImageFiles={handleUploadImageFiles}
                        isLoading={isLoading}
                    />
                </Col>
            </Row>
        </Container>
    )
}

export default AnalysisPage;