import { Button, Card, Col, Form, InputGroup, Row } from "react-bootstrap";

function FileController({userFiles, selectedFolder, handleFileInput, handleUploadImageFiles, isLoading}) {

    const resultText = {
        folderUploadRequest: `"  "     .`,
        folderSelectRequest: `      ""  .`
    }

    const printResultText = () => {
        if(!Object.keys(userFiles).length){
            return resultText.folderUploadRequest;
        }else if(!Object.keys(selectedFolder).length){
            return resultText.folderSelectRequest;
        }
    }

    return (
        <Card className="shadow CustomCard">
            <Card.Body className="ControllerCardBody">
                <Row>
                    <InputGroup>
                        <InputGroup.Text>  </InputGroup.Text>
                        <Form.Control
                            placeholder={`${Object.keys(selectedFolder).length ? selectedFolder.path : "..."}`}
                            disabled
                        />
                    </InputGroup>
                </Row>               
                <Row>
                    <Col className="col-6">
                        <Button className="w-100" variant="success" onClick={handleFileInput} disabled={isLoading}>
                            {isLoading ? "  .." : "  "}                            
                        </Button>
                    </Col>
                    <Col className="col-6">
                        <Button className="w-100" onClick={handleUploadImageFiles}></Button>
                    </Col>
                </Row>
            </Card.Body>
            <Card.Footer>
                <Row>
                    <Col>
                        <Card className="CustomCard AnalysisResultWindow">
                            <Card.Body>                                
                                {printResultText()}
                            </Card.Body>
                        </Card>
                    </Col>                    
                </Row>
            </Card.Footer>
        </Card>
    );
}

export default FileController;